    "bio": "Antonella Marlene Milano: Arquitecta / Urbanista / Experta en Participacin / Ecosistema Urbano<br>Desde 2013 colaboro con Ecosistema Urbano, estudio de arquitectura y urbanismo con oficinas en Madrid, Espaa, Boston y Miami, EE.UU. como coordinadora de proyectos. En el estudio soy responsable de las estrategias urbanas y del desarrollo de metodologas innovadoras que integran los mecanismos de participacin y las herramientas digitales en los procesos de diseo. Con Ecosistema Urbano soy consultora de entidades como el Banco Mundial y el Banco Interamericano de Desarrollo para proyectos que van desde la definicin de estrategias urbanas y territoriales hasta la implementacin de proyectos piloto para la activacin del espacio pblico.<br>Mi experiencia en procesos participativos empez hace ms de diez aos, en el contexto de Apulia, Italia, con la organizacin de innovadoras acciones urbanas de activacin del espacio pblico."
